import {
  Button,
  Card,
  CardBody,
  Pagination,
  Select,
  Table,
  TableCell,
  TableContainer,
  TableFooter,
  TableHeader,
} from "@windmill/react-ui";
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

//internal import
import useAsync from "hooks/useAsync";
import useFilter from "hooks/useFilter";
import OrderServices from "services/OrderServices";
import ordersfromlocal from "../../utils/vendorsInvoceData";
import NotFound from "components/table/NotFound";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import OrderTable from "components/vendors/vendorInvoiceTable";
import TableLoading from "components/preloader/TableLoading";
import { FiPlus } from "react-icons/fi";
import { Link } from "react-router-dom";
import { useParams } from "react-router";
import { getVendorInvoiceList } from "services/Api";

const Orders = () => {
  const {
    time,
    currentPage,
    searchText,
    status,

    handleSubmitForAll,
    resultsPerPage,
    startDate,
    endDate,
    lang,
  } = useContext(SidebarContext);

  const { t } = useTranslation();

  const [paymentType, setPaymentType] = useState("paid");
  const [pageNumber, setPageNumber] = useState(1);
  const [vendorInvoiceData, setVendorInvoiceData] = useState({});
  const [loading, setLoading] = useState(false);

  console.log(vendorInvoiceData, "vendor_invoice");
  const { id } = useParams();

  const getVenderInvoices = async (paymentType, pageNumber, id) => {
    const res = await getVendorInvoiceList(paymentType, pageNumber, id);

    if (res.status === 200) {
      setVendorInvoiceData(res?.data?.data);
    }
    setLoading(false);
  };

  useEffect(() => {
    setLoading(true);
    getVenderInvoices(paymentType, pageNumber, id);
  }, [paymentType]);

  const handleChangePage = (page) => {
    // setLoading(true);
    getVenderInvoices(paymentType, page, id);
    setPageNumber(page);
  };
  // console.log("data in orders page", data);

  return (
    <>
      <PageTitle>{t("Vendor Invoice")}</PageTitle>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex justify-start xl:w-1/2  md:w-full"></div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to={`/add-invoice/${id}`}>
                  {" "}
                  <Button className="w-full rounded-md h-12">
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Invoice")}
                  </Button>{" "}
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>
      <div className="w-full md:w-48 lg:w-48 xl:w-48 mb-5">
        <Select
          className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
          value={paymentType}
          onChange={(e) => setPaymentType(e.target.value)}
        >
          <option value="paid">Paid</option>
          <option value="unpaid">Unpaid</option>
        </Select>
      </div>
      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : vendorInvoiceData?.vendor_invoice?.length !== 0 ? (
        <TableContainer className="mb-8 dark:bg-gray-900">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{t("Sl No.")}</TableCell>
                <TableCell>{t("Vendor Name")}</TableCell>
                <TableCell>{t("Product / Service")}</TableCell>
                <TableCell>{t("Description")}</TableCell>
                <TableCell>{t("Invoice Date")}</TableCell>
                <TableCell>{t("Invoice Total")}</TableCell>

                <TableCell>{t("Scan Copy")}</TableCell>
                <TableCell>{t("Type")}</TableCell>
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>

            <OrderTable
              lang={lang}
              pid={id}
              paymentType={paymentType}
              pageNumber={pageNumber}
              getVenderInvoices={getVenderInvoices}
              orders={vendorInvoiceData?.vendor_invoice}
              currency={"$"}
            />
          </Table>

          <TableFooter>
            <Pagination
              totalResults={vendorInvoiceData?.pagination?.total}
              resultsPerPage={vendorInvoiceData?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Table navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Sorry, There are no invoices right now." />
      )}
    </>
  );
};

export default Orders;
