import { TableBody, TableCell, TableRow,Button } from "@windmill/react-ui";
import PrintReceipt from "components/form/PrintReceipt";
import SelectStatus from "components/form/SelectStatus";
import Status from "components/table/Status";
import Tooltip from "components/tooltip/Tooltip";
import { useTranslation } from "react-i18next";
import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
import { Link } from "react-router-dom";
import { showDateTimeFormat } from "utils/dateFormate";
import image1 from "../../assets/img/day1.png";
import image2 from "../../assets/img/day2.png";
import useToggleDrawer from "hooks/useToggleDrawer";
import DeleteModal from "./Deleteinvoicelist";
import moment from "moment";
const OrderTable = ({ orders, currency, globalSetting,getVenderInvoices, paymentType, pageNumber, pid }) => {
  const { t } = useTranslation();
  const { title, serviceId, handleModalOpen, handleUpdate } = useToggleDrawer();

  return (
    <>
      <DeleteModal paymentType={paymentType} pageNumber={pageNumber} pid={pid} id={serviceId} title={title} getVenderInvoices={getVenderInvoices}/>
      <TableBody className="dark:bg-gray-900">
        {orders?.map((order, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="font-semibold uppercase text-xs">{i + 1}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">{order?.Vendor?.company_name}</span>
            </TableCell>

            <TableCell className="text-xs">
              <span className="text-sm">{order?.Vendor?.service_category}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm">
                {order?.Vendor?.service_description}
              </span>
            </TableCell>

            <TableCell>
              <span className="text-sm">
                {order?.invoice_date &&
                  moment(order?.invoice_date).format("DD-MM-YYYY")}
              </span>
            </TableCell>

            <TableCell className="text-sm">{order?.total_amount}</TableCell>

            <TableCell className="text-center">
              {order?.bill_file_path && (
                <img src={order?.bill_file_path} className="mx-2" alt="lang" />
              )}
                <span className="text-sm">  
              <a href={order?.bill_file_path} target="_blank">
              <Button className="button-style">View</Button>
              </a></span>
            </TableCell>
            <TableCell className="text-center">{order?.status}</TableCell>
            <TableCell className="text-right flex justify-end">
              <div className="flex justify-between items-center">
                {/* <PrintReceipt orderId={order._id} />

                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/vendor-invoice/${order._id}`}>
                    <Tooltip
                      id="view"
                      Icon={FiZoomIn}
                      title={t("ViewInvoice")}
                      bgColor="#059669"
                    />
                  </Link>
                </span> */}
                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  <Link to={`/edit-invoice/${order.id}`}>
                    <Tooltip
                      id="view"
                      Icon={FiEdit}
                      title={t("ViewInvoice")}
                      bgColor="#059669"
                    />
                  </Link>
                </span>
                <button
                  onClick={() =>
                    handleModalOpen(order.id, "Invoice", "product")
                  }
                  className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
                >
                  <Tooltip
                    id="delete"
                    Icon={FiTrash2}
                    title={t("Delete")}
                    bgColor="#EF4444"
                  />
                </button>
              </div>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default OrderTable;
